import {mapState} from 'vuex'
import {cmsIndexIndex,indexSearch} from '../../../api/find.js'
export default {
	data() {
		return {
			searchValue:"",//
			tabs:[],//tabs
			nowTabIndex:0,//tab
			fiveList:[],
			isShow:false,//
			page:1,//
			status: 'loadmore',//
			iconType: 'flower',
			loadText: {
				loadmore: '',
				loading: '',
				nomore: ''
			}
		}
	},
	computed: {
		...mapState(['statusBarHeight']),
	},
	methods: {
		// 
		changeTab(index){
			this.nowTabIndex=index
			this.page=1
			this.fiveList=[]
			this.status='loadmore'
			cmsIndexIndex({page:this.page,channel_id:this.nowTabIndex}).then(res=>{
				this.fiveList=this.fiveList.concat(res.data.data)//
				this.status=res.data.haspage=='false'?'nomore':'loadmore'//
			})
		},
		// 
		toPutQuestions(){
			uni.navigateTo({
				url:"/pages/problem/put_questions/put_questions"
			})
		},
		// 
		getSearch(){
			if(this.searchValue==""){
				this.isShow=false
				this.getIndexInfo()
			}else{
				this.page=1
				this.fiveList=[]
				this.status='loadmore'
				indexSearch({search:this.searchValue,page:this.page}).then(res=>{
					this.isShow=true
					this.fiveList=this.fiveList.concat(res.data.data)
					this.status=res.data.haspage=='false'?'nomore':'loadmore'//
				})
			}
		},
		// 
		onReachBottom() {
			if(this.status=='loadmore'){
				this.page=this.page+1
				if(this.isShow){
					indexSearch({search:this.searchValue,page:this.page}).then(res=>{
						this.isShow=true
						this.fiveList=this.fiveList.concat(res.data.data)
						this.status=res.data.haspage=='false'?'nomore':'loadmore'//
					})
				}else{
					cmsIndexIndex({page:this.page,channel_id:this.nowTabIndex}).then(res=>{
						this.fiveList=this.fiveList.concat(res.data.data)//
						this.status=res.data.haspage=='false'?'nomore':'loadmore'//
					})
				}
			}else{
				this.$util.tips({title:"~"})
			}
		},
		// 
		getIndexInfo(){
			this.page=1
			this.fiveList=[]
			this.status='loadmore'
			cmsIndexIndex({page:this.page,channel_id:""}).then(res=>{
				this.tabs=res.data.tabList//tab
				this.nowTabIndex=res.data.tabList[0].id//id
				this.fiveList=res.data.data//
				this.status=res.data.haspage=='false'?'nomore':'loadmore'//
			})
		}
	},
	onShow(){
		this.searchValue=""//
		// 
		this.getIndexInfo()
	},
	onLoad(){

	},
}