import {mapState} from 'vuex'
import {indexSearch,searchHistoryList,searchHistory,searchListDel} from "../../../api/find.js"
export default {
	data() {
		return {
			searchValue:"",// 
			page:1,//
			channel:[],//
			retrieval:[],//
			history:[],//
			isShow:true,//
			status: 'loadmore',//
			iconType: 'flower',
			loadText: {
				loadmore: '',
				loading: '',
				nomore: ''
			}
		}
	},
	computed: {
		...mapState(['statusBarHeight']),
	},
	methods: {
		// 
		toSpecialColumn(item){
			uni.navigateTo({
				url:"/pages/problem/special_column/special_column?id="+item.id
			})
		},
		// indexSearc
		getSearch(){
			if(this.searchValue==""){
				this.isShow=true
			}else{
				this.isShow=false//
				this.page=1//
				this.channel=[]//
				this.retrieval=[]//
				this.status='loadmore'//
				indexSearch({search:this.searchValue,page:this.page}).then(res=>{
					this.channel=this.channel.concat(res.data.channel)//
					this.retrieval=this.retrieval.concat(res.data.data)//
					this.status=res.data.haspage=='false'?'nomore':'loadmore'//
				})
			}
		},
		// 
		toDetail(item){
			// type012345
			if(item.type==1||item.type==2){
				uni.navigateTo({
					url:"/pages/live_broadcast/video_playback/video_playback"
				})
				// 
				searchHistory({type:2,id:item.id}).then(res=>{console.log(res)})
			}else if(item.type==3||item.type==4){
				uni.navigateTo({
					url:"/pages/problem/article_details/article_details?id="+item.id
				})
				// 
				searchHistory({type:1,id:item.id}).then(res=>{console.log(res)})
			}
		},
		// 
		onReachBottom() {
			if(this.status=='loadmore'){
				this.page=this.page+1
				indexSearch({search:this.searchValue,page:this.page}).then(res=>{
					this.channel=this.channel.concat(res.data.channel)//
					this.retrieval=this.retrieval.concat(res.data.data)//
					this.status=res.data.haspage=='false'?'nomore':'loadmore'//
				})
			}else{
				this.$util.tips({title:"~"})
			}
		},
		// 
		getHistoryList(){
			searchHistoryList().then(res=>{
				if(res.data!=null){
					this.history=res.data
				}else{
					this.history=[]
				}
			})
		},
		// 
		delInfo(item,type){
			searchListDel({ids:item.id,type:type}).then(res=>{
				this.getHistoryList()
			})
		}
	},
	onLoad(options){
		// 
		this.getHistoryList()
	},
}