import {mapState} from 'vuex'
import {collectList,myQuestion,viewsHistory,collectListDel,viewsHistoryDel} from '../../../api/my.js'
export default {
	data() {
		return {
			type:0,//012
			title:"",
			titleRight:false,//
			page:1,//
			lock:"true",//
			list: {},//
			status: 'loadmore',//
			iconType: 'flower',
			loadText: {
				loadmore: '',
				loading: '',
				nomore: ''
			},
			disabled: false,//
			// 
			options: [
				{
					text: '',
					style: {
						backgroundColor: '#dd524d',
						fontSize:"30rpx",
						color:"#FFFFFF",
						textAlign:"center",
					}
				}
			],
			allCheck:false,//
			ids:[],//
		}
	},
	computed: {
		...mapState(['statusBarHeight']),
		newList(){
			return this.list
		}
	},
	methods: {
		// 
		click(index,key) {
			if(this.type==0){
				collectListDel({ids:this.list[key][index].aid}).then(res=>{
					this.list[key].splice(index, 1);
				})
			}else{
				viewsHistoryDel({ids:this.list[key][index].aid}).then(res=>{
					this.list[key].splice(index, 1);
				})
			}
		},
		// 
		delMore(){
			this.page=1//
			this.lock="true"//
			this.list={}//
			this.status="loadmore"//
			if(this.type==0){
				collectListDel({ids:this.ids.join(",")}).then(res=>{this.getList();this.changeRight()})
			}else{
				viewsHistoryDel({ids:this.ids.join(",")}).then(res=>{this.getList();this.changeRight()})
			}
		},
		// 
		open(index,key) {
			// swipeActionprops
			// 'false''false'
			this.list[key][index].show = true;
		},
		// 
		checkboxChange(e){
			if(e.name==""){
				// 
				for(let key in this.list){
					this.list[key].forEach(item=>{
						item.checked=e.value
					})
				}
				if(e.value){//
					this.ids=[]
					for(let key in this.list){
						this.list[key].forEach(item=>{
							this.ids.push(item.aid)
						})
					}
				}else{//
					this.ids=[]
				}
			}else{
				if(e.value){
					this.ids.push(e.name)//
					var lengthList=0
					for(let key in this.list){//
						lengthList=lengthList+this.list[key].length
					}
					if(this.ids.length==lengthList){//
						this.allCheck=true
					}
				}else{
					if(this.ids.indexOf(e.name)!=-1){
						this.ids.splice(this.ids.indexOf(e.name),1)//
					}
					this.allCheck=false//
				}
			}
		},
		// 
		changeRight(){
			for(var item in this.list){
				this.list[item].forEach(item=>{
					item.show=false
				}) 
			} 
			this.titleRight=!this.titleRight
			// 
			this.disabled=this.titleRight
		},
		// 
		getList(){
			if(this.lock=="true"){
				if(this.type==0){
					// 
					collectList({page:this.page}).then(res=>{
						this.changeList(res.data)
					})
				}else{
					// 
					viewsHistory({page:this.page}).then(res=>{
						this.changeList(res.data)
					})
				}
			}else{ 
				this.$util.tips({title:""})
			}
		},
		// 
		changeList(data){
			this.lock=data.pagenext//
			this.status=this.lock=='false'?'nomore':'loadmore'//
			delete data.pagenext
			for(let key in data){
				data[key].forEach(item=>{
					item.create_time=this.$util.format(item.create_time,3,"/")
					item.show=false
					item.checked=false
				})
			}
			// this.list=data//
			for(let key in data){
				if(this.list.hasOwnProperty(key)==true){
					this.list[key]=this.list[key].concat(data[key])
				}else{
					this.list={...this.list,[key]:data[key]}
				}
			}
		},
		// 
		onReachBottom() {
			this.page=this.page+1
			this.getList()
		},
		toDetail(item,index){
			uni.navigateTo({
				url:"/pages/problem/article_details/article_details?id="+item[index].aid
			})
		}
	},
	onLoad(options){
		// 
		this.type=options.type
		if(options.type==0){
			this.title=""
		}else{
			this.title=""
		}
		// 
		this.getList()
	},
	onShow(){

	}
}