import {mapState} from 'vuex'
import images from '../../../util/images.js'
import {channelInfo,userChannelCollert,channelTab} from '../../../api/problem.js'
export default {
	data() {
		return {
			background: {backgroundColor: 'rgba(0,0,0,0)',},
			channel_info:{},//
			channel_count:0,//
			channel_number:0,//
			tabs:[],//
			nowTabIndex:0,//tab
			list:[],//
			page:1,
			channel_collect:true,//
			status: 'loadmore',//
			iconType: 'flower',
			loadText: {
				loadmore: '',
				loading: '',
				nomore: ''
			}
		}
	},
	computed: {
		...mapState(['statusBarHeight']),
	},
	methods: {
		// tab
		changeTab(index){
			this.nowTabIndex=index//tabs
			this.list=[]
			this.page=1
			this.getData()
		}, 
		// 
		toQAndA(item){
			if(this.nowTabIndex==1){//
				uni.navigateTo({
					url:"/pages/problem/question_and_answer/question_and_answer?id="+item.id+"&channel_id="+this.channel_info.id
				})
			}else if(this.nowTabIndex==3){//
				uni.navigateTo({
					url:"/pages/problem/article_details/article_details?id="+item.id
				})
			}else if(this.nowTabIndex==2){//
				uni.navigateTo({
					url:"/pages/live_broadcast/video_playback/video_playback?id="+item.id
				})
			}else{
				if(item.type==1||item.type==2){
					uni.navigateTo({
						url:"/pages/live_broadcast/video_playback/video_playback?id="+item.id
					})
				}else if(item.type==3||item.type==4){
					uni.navigateTo({
						url:"/pages/problem/article_details/article_details?id="+item.id
					})
				}
			}
		},
		// 
		ChannelCollect(){
			//channel_collecttrue10
			userChannelCollert({type:this.channel_collect?1:0,channel_id:this.channel_info.id}).then(res=>{
				console.log(res)
				this.channel_collect=!this.channel_collect
			})
		},
		// 
		getData(){
			channelTab({type:this.tabs[this.nowTabIndex],page:this.page,id:this.channel_info.id}).then(res=>{
				res.data.data.forEach(item=>{//
					item.updatetime=this.$util.format(item.updatetime,3,"/")
				})
				this.list=this.list.concat(res.data.data)
				this.status=res.data.haspage=='false'?'nomore':'loadmore'
			})
		},
		// 
		onReachBottom(){
			if(this.status=='loadmore'){
				this.page=this.page+1
				this.getData()
			}else{
				this.$util.tips({title:"~"})
			}
		}
	},
	onLoad(options){
		channelInfo({id:options.id}).then(res=>{
			this.channel_info=res.data.channel_info//
			this.tabs=res.data.channel_table//
			this.channel_count=res.data.channel_count//
			this.channel_number=res.data.channel_number//
			res.data.channel_archives.forEach(item=>{//
				item.updatetime=this.$util.format(item.updatetime,3,"/")
			})
			this.list=res.data.channel_archives//
			this.channel_collect=res.data.channel_collect=="0"?true:false//01
		})
	},
}