import {mapState} from 'vuex'
import images from '../../../util/images.js'
import {userRegister,getcode,smsLogin,changepassword,loginh5} from '../../../api/login.js'
export default {
	data() {
		return {
			allImage:images,
			// form
			form:{
				phone:"",
				password:"",
				determine_password:"",
				code:""
			},
			isRadio:true,//
			passwordPla:"",//
			type:0,
			btn:"",//
			code:"",//
			passwordAndCod:"",
			isLockCode:true,//
			countdown:60,//
			nowPage:-1,//
		}
	},
	computed: {
		...mapState(['statusBarHeight']),
	},
	methods: {
		// 
		formSubmit: function(e) {
			if(!this.$util.regular('phone',this.form.phone)){this.$util.tips({title:''});return}//
			if(this.type!=2){if(this.form.password==""){this.$util.tips({title:""});return}}//
			if(this.type==3){if(this.form.determine_password!=this.form.password){this.$util.tips({title:''});return}}//
			if(this.type!=1){if(this.form.code==""){this.$util.tips({title:""});return}}//
			if(!this.isRadio){this.$util.tips({title:""});return}
			var nowData=e.detail.value//form
			if(this.type==0){//
				userRegister(Object.assign(nowData,uni.getStorageSync('register'))).then(res=>{
					uni.setStorageSync('user',res.data)//
					this.$store.dispatch('changeAvatar',uni.getStorageSync("user").avatar)
					this.$util.tips({title:res.msg},{tab:1,url:'/pages/index/index/index'})
				})
			}else if(this.type==2){//
				smsLogin(nowData).then(res=>{
					uni.setStorageSync('user',res.data)//
					this.$store.dispatch('changeAvatar',uni.getStorageSync("user").avatar)
					this.$util.tips({title:res.msg},{tab:1,url:'/pages/index/index/index'})
				})
			}else if(this.type==3){// 
				changepassword(nowData).then(res=>{
					uni.setStorageSync('user',res.data)//
					this.$store.dispatch('changeAvatar',uni.getStorageSync("user").avatar)
					this.$util.tips({title:res.msg},{tab:1,url:'/pages/index/index/index'})
				})
			}else if(this.type==1){
				loginh5(nowData).then(res=>{
					uni.setStorageSync('user',res.data)//
					this.$store.dispatch('changeAvatar',uni.getStorageSync("user").avatar)
					this.$util.tips({title:res.msg},{tab:1,url:'/pages/index/index/index'})
				})
			}
		},
		// 
		getCode(){
			if(this.isLockCode){
				this.isLockCode=false//
				if(!this.$util.regular('phone',this.form.phone)){
					this.$util.tips({title:''})
					this.isLockCode=true
					return
				}
				getcode({mobile:this.form.phone,type:this.nowPage}).then(res=>{
					console.log(res)
				})
				this.settime()
			}else{
				this.$util.tips({title:"~"})
			}
		},
		// 
		settime() {
			var this_=this
			var time=setInterval(function(){
				this_.countdown=this_.countdown-1
				this_.code=this_.countdown+'s'
				if(this_.countdown==0){
					this_.isLockCode=true
					this_.code=""
					this_.countdown=60
					clearInterval(time)
				}
			},1000)
		},
		// 
		changeRadio(){
			this.isRadio=!this.isRadio
		},
		// 
		goForget(index){
			if(this.type==2){//
				uni.navigateTo({
					url:"/pages/login/sign_in/sign_in?type=1"
				})
			}else{//
				uni.navigateTo({
					url:"/pages/login/sign_in/sign_in?type="+index
				})
			}
		},
		// 
		toAgreement(type){
			uni.navigateTo({
				url:"/pages/login/agreement/agreement?type="+type
			})
		}
	},
	onLoad(options){
		this.type=options.type
		//01
		if(options.type==0){//
			this.passwordPla=""
			this.btn=""
			this.nowPage=1
		}else if(options.type==1){//
			this.passwordPla=""
			this.btn=""
		}else if(options.type==2){//
			this.passwordPla=""
			this.btn=""
			this.passwordAndCod=""
			this.nowPage=0
		}else{//
			this.passwordPla=""
			this.btn=""
			this.nowPage=2
		}
	},
}