import {mapState} from 'vuex'
import {userInfo,editUserinfo,incomeList,housePrice,houseConfig,houseCategory,carPrice,carConfig,carCategory,houseCategoryList,shebaoList} from '../../../api/personal_center.js'
export default {
	data() {
		return {
			headStyle:{//
				fontSize:'30rpx',
				color:"#333333",
				fontWeight:"bold",
				borderBottom:"1rpx solid #E1E1E1",
				padding:"30rpx 0rpx",
			},
			carShow:false,//
			homeShow:false,//
			slotRight:false,//
			newCarInfo:[],//
			newHomeInfo:[],//
			newUserInfo:{},//
			onlyCar:{},//
			onlyHome:{},//
			timeAddressShow:false,//
			timeOrAddress:"time",//
			selectorShow:false,//
			oneListIndex:0,//
			selector:[],//
			nowPage:0,////
			carHomeTitle:"",//
			subscript:-1,//
		}
	},
	computed: {
		...mapState(['statusBarHeight'])
	},
	methods: {
		// 
		changeImage(){
			if(this.slotRight){
				var that=this
				uni.chooseImage({
					count:1,
					sizeType: ['compressed'],//
					sourceType: ['album','camera'], //
				    success: function (res) {
						const tempFilePaths = res.tempFilePaths;
						uni.uploadFile({
							url: 'https://mituo.xypvip.cn/addons/cms/wxapp.index/image', //
							filePath: tempFilePaths[0],
							name: 'file',
							fileType:"image",
							success: (res) => {
								that.newUserInfo.avatar=JSON.parse(res.data).data
							},
						});
				    }
				});
			}
		},
		// 
		changeCarHome(index,carHome){
			console.log(index,carHome,this.newCarInfo,this.newHomeInfo)
			if(index==0){
				this.carShow=!this.carShow
			}else{
				this.homeShow=!this.homeShow
			}
		},
		// 
		editPreservation(){
			this.slotRight=!this.slotRight
			if(!this.slotRight){//
				var data={
					avatar:this.newUserInfo.avatar,//
					username:this.newUserInfo.username,//
					gender:this.newUserInfo.gender==0?'':'',//
					birthday:this.newUserInfo.birthday,//
					mobile:this.newUserInfo.mobile,//
					city:this.newUserInfo.city,//
					income:this.newUserInfo.income,//
					ishas_shebao:this.newUserInfo.ishas_shebao,//
					shebao:this.newUserInfo.shebao,//
					carInfo:this.newCarInfo,//
					homeInfo:this.newHomeInfo,//
				}
				editUserinfo(data).then(res=>{
					var user=uni.getStorageSync("user")
					user.avatar=this.newUserInfo.avatar
					uni.setStorageSync("user",user)
					this.$store.dispatch('changeAvatar',uni.getStorageSync("user").avatar)
				})
			}
		},
		// 
		changeShow(text){
			if(this.slotRight){
				this.timeOrAddress=text,//
				this.timeAddressShow=!this.timeAddressShow//
			}
		},
		// 
		changeShow2(index){
			if(this.slotRight){
				this.selector=[]
				this.selectorShow=!this.selectorShow//
				this.oneListIndex=index//
				if(index==0){
					this.selector=["",""]
				}else if(index==1){//
					incomeList().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==2){
					this.selector=["",""]
				}else if(index==3){//
					shebaoList().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==4){//
					carPrice().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==5){//
					carConfig().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==6){//
					houseCategoryList().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==7){//
					housePrice().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==8){//
					houseConfig().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==9){//
					houseCategory().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}else if(index==10){//
					carCategory().then(res=>{res.data.forEach(item=>{this.selector.push(item.text)})})
				}
			}
		},
		// 
		toAdd(index){
			if(this.nowPage==1){
				if(!this.onlyCar.marketValue){this.$util.tips({title:""});return}
				if(!this.onlyCar.periodNumber){
					this.$util.tips({title:""});
					return
				}else if(this.onlyCar.periodNumber!=""){
					if(!this.onlyCar.periodCategory){this.$util.tips({title:""});return}
				}
				if(!this.onlyCar.licensePlate){this.$util.tips({title:""});return}
				if(!this.onlyCar.kilometers){this.$util.tips({title:""});return}
				if(this.subscript==-1){
					this.newCarInfo.push(this.onlyCar)
				}else{
					this.newCarInfo.splice(this.subscript,1,this.onlyCar)
					this.subscript=-1
				}
			}else if(this.nowPage==2){
				if(!this.onlyHome.type){this.$util.tips({title:""});return}
				if(!this.onlyHome.marketValue){this.$util.tips({title:""});return}
				if(!this.onlyHome.periodNumber){
					this.$util.tips({title:""});
					return
				}else if(this.onlyHome.periodNumber!=""){
					if(!this.onlyHome.periodCategory){this.$util.tips({title:""});return}
				}
				if(this.subscript==-1){
					this.newHomeInfo.push(this.onlyHome)
				}else{
					this.newHomeInfo.splice(this.subscript,1,this.onlyHome)
					this.subscript=-1
				}
			}
			this.nowPage=index
			if(index==1){
				this.onlyCar={}
				this.carHomeTitle=""
			}else if(index==2){
				this.onlyHome={}
				this.carHomeTitle=""
			}
		},
		//  
		addressTime(item){ 
			if(this.timeOrAddress=="time"){
				this.newUserInfo.birthday=item.year+"-"+item.month+"-"+item.day//
			}else{
				this.newUserInfo.city=item.province.label+item.city.label+item.area.label//
			}
		},
		// 
		oneChange(value){ 
			var index=value[0]//
			if(this.oneListIndex==0){//
				this.newUserInfo.gender=this.selector[index]==""?"1":"0"
			}else if(this.oneListIndex==1){//
				this.newUserInfo.income=this.selector[index]
			}else if(this.oneListIndex==2){//
				this.newUserInfo.ishas_shebao=this.selector[index]
			}else if(this.oneListIndex==3){//
				this.newUserInfo.shebao=this.selector[index]
			}else if(this.oneListIndex==4){//
				this.onlyCar.marketValue=this.selector[index]
			}else if(this.oneListIndex==5){//
				this.onlyCar.periodNumber=this.selector[index]
			}else if(this.oneListIndex==6){//
				this.onlyHome.type=this.selector[index]
			}else if(this.oneListIndex==7){//
				this.onlyHome.marketValue=this.selector[index]
			}else if(this.oneListIndex==8){//
				this.onlyHome.periodNumber=this.selector[index]
			}else if(this.oneListIndex==9){//
				this.onlyHome.periodCategory=this.selector[index]
			}else if(this.oneListIndex==10){//
				this.onlyCar.periodCategory=this.selector[index]
			}
		},
		// 
		toCarHome(index,subscript){
			this.nowPage=index//
			this.subscript=subscript//
			if(index==1){
				this.carHomeTitle=""
				this.onlyCar.marketValue=this.newCarInfo[subscript].marketValue
				this.onlyCar.periodNumber=this.newCarInfo[subscript].periodNumber
				this.onlyCar.periodCategory=this.newCarInfo[subscript].periodCategory
				this.onlyCar.licensePlate=this.newCarInfo[subscript].licensePlate
				this.onlyCar.kilometers=this.newCarInfo[subscript].kilometers
			}else if(index==2){
				this.carHomeTitle=""
				this.onlyHome.type=this.newHomeInfo[subscript].type
				this.onlyHome.marketValue=this.newHomeInfo[subscript].marketValue
				this.onlyHome.periodNumber=this.newHomeInfo[subscript].periodNumber
				this.onlyHome.periodCategory=this.newHomeInfo[subscript].periodCategory
			}
		}
	},
	onLoad(){
		userInfo().then(res=>{
			this.newCarInfo=JSON.parse(JSON.stringify(res.data.car_info))//
			this.newHomeInfo=JSON.parse(JSON.stringify(res.data.house_info))//
			this.newUserInfo=JSON.parse(JSON.stringify(res.data.user_info))//
		})
	},
	onShow(){
	}
}