import {mapState} from 'vuex'
import images from '../../../util/images.js'
import {indexIndexs} from '../../../api/index.js'
export default {
	data() {
		return {
			allImages:images,//
			live:{},//
			fiveList:[]// 0,1,2,3,4,5,
		}
	},
	computed: {
		...mapState(['statusBarHeight','avatar']),
	},
	methods: {
		// 
		toSearch(){
			uni.navigateTo({
				url:'/pages/problem/search/search'
			})
		},
		//  
		toLiveBroadcast(id){
			uni.navigateTo({
				url:"/pages/live_broadcast/live_broadcast/live_broadcast?id="+id
			})
		}
	},
	onShow(){
		// 
		indexIndexs().then(res=>{
			this.live=res.data.live//
			this.fiveList=res.data.data//
		})
		console.log(this.avatar)
	},
	onPullDownRefresh() {
		// 
		indexIndexs().then(res=>{
			this.live=res.data.live//
			this.fiveList=res.data.data//
		})
	}
}